import { createRouter, createWebHistory } from "vue-router";


import Home from '@/pages/Home.vue'

const router = createRouter({
    history: createWebHistory(),
    routes:[
        {path:'/jsonplaceholder', name:'home', component:Home},
        {path:'/jsonplaceholder/users', name:'users', component:()=>import('@/pages/Users.vue')},
        {path:'/jsonplaceholder/users/:id', name:'usersId', component:()=>import('@/pages/UserId.vue')},
        {path:'/jsonplaceholder/posts', name:'posts', component:()=>import('@/pages/Posts.vue')},
        {path:'/jsonplaceholder/posts/:id', name:'postsId', component:()=>import('@/pages/PostId.vue')},
        {path:'/jsonplaceholder/todos', name:'todos', component:()=>import('@/pages/Todos.vue')},
    ]
})

export default router